    import {mapState, mapMutations} from 'vuex';
	import {getInfo,getDyn} from '../../api/api.js';

    export default {
		data() {
			return {
				userInfo: '',
				dynInfo: '',
				statusInfo: '',
				uid: ''
			}
		},
        methods: {
			getUserInfo() {
				getInfo(this.uid).then(data => {
					this.userInfo = data;
				});
				getDyn(this.uid).then(data => {
					this.dynInfo = data.resp;
					// console.log(this.dynInfo);
				});
			},
			preview(type,index) {
				let imgUrl = [];
				if(type == 'por') imgUrl = [this.userInfo.portrait];
				if(type == 'dyn') imgUrl = this.dynInfo;
				uni.previewImage({
					current:index,
					urls: imgUrl,
					longPressActions: {
						itemList: [],
						success: function(data) {
							console.log('选中了第' + (data.tapIndex + 1) + '个按钮,第' + (data.index + 1) + '张图片');
						},
						fail: function(err) {
							console.log(err.errMsg);
						}
					}
				});
			}
        },
		onLoad(option) {
			// console.log(option.uid);
			this.uid = option.uid;
		},
		onShow() {
			this.getUserInfo();
		}
    }
